import React, { useRef } from 'react'
import {Link, Outlet} from 'react-router-dom';
import '../Styles/Projects.scss';

export default function Projects() {
  return (
    <section className='projects'>
      <div className="projects__header" style={{display: "flex", gap: "20px"}}>

        <Link to="/projects/art" className="header__art header__link">Art</Link>
        <Link to="/projects/installation" className="header__installation header__link">Installation</Link>
        <Link to="/projects/architecture" className="header__architecture header__link">Architecture</Link>
        <Link to="/projects/commercial" className="header__commercial header__link">Commercial</Link>
        <Link to="/projects/ai" className="header__ai header__link">AI</Link>

      </div>

      <Outlet />
    </section>
  )
}
