import React, {useState, useMemo, useEffect} from 'react'
import "../Styles/Art.scss";
import Masonry from 'react-smart-masonry';
import { useLocation, Link } from 'react-router-dom';
import Upload from './Upload/Upload';
import Hover from './Hover/Hover';
import { getPosts } from './firebase';

const data = [
  {id: 0, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 1, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 2, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 3, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 4, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 5, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 6, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
  {id: 7, src: "https://picsum.photos/200/320", name: "Lorem, ipsum dolor."},
]

export default function Art(props) {
  const [value, setValue] = useState(0);

  const location = "Art";
  const isEdit = props.isEdit;

  useEffect(()=>{
    window.dispatchEvent(new Event("resize"));
  })

  const [posts, setPosts] = useState([]);

  useMemo(()=>{
    getPosts(location, (posts)=>{
      setPosts(posts.sort((image1, image2)=>{
        return image1.order - image2.order;
      }));
    })
  },[value])
  

  return (
    <div className='art'>
      <div className="container">
        <Masonry
        className="art__item-block"
        columns={{tablet: 2, desktop: 3}}
        breakpoints={{tablet: 0, desktop: 767}} 
        gap={10}
        autoArrange={true}
        >
          {
            posts.map(((item, index)=>{
              return(
                <div className='post'>
                  <Link key={index} className="installation__item" to={`/projects/art/${item.name}`} >
                    <img height={640} width={400} src={item.images && Object.values(item.images)[0].url || "#"} className="art__image"  alt="" />
                    <p className="art__title">{item.name}</p>
                  </Link>
                  {isEdit && (
                  <Hover location={location} update={()=>{setValue(value+1)}} index={index} image={item} images={posts} />)}
                </div>
              )
            }))
          }
        </Masonry>
        {isEdit && (
          <Upload type="album" location={location} callback={()=>{setValue(value+1)}} />
        )}
      </div>
    </div>
  )
}